.body-product-container {
    width: 75%;
    background-color: white;
    position: relative;
}

.body-product-header {
    background-color: white;
    display: flex;
    flex-direction: row;
}

.body-product-tab {
    padding: 1rem;
    font-weight: bold;
}

.body-product-tab:hover {
    cursor: pointer;
}

.active {
    background-color: white;
    border-top: 2px solid var(--main-bg-color);
}
@media screen and (max-width: 1280px) {
    .body-product-container {
        width: 100%;
    }
}

@media screen and (max-width: 768px){
    .body-product-container {
        width: 100%;
    }
    .body-product-tab{
        font-size: 1rem;
        padding: 0.7rem;
    }
}