.style-spec-1 {
    padding: 2em 1em;
    animation: opacity-spec 0.5s ease-out;
}
.table table tbody tr td:nth-child(even){
    height: 2rem;
    width: 20rem;
}
.table table {
    left: 0;
}
@keyframes opacity-spec {
    0%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@-webkit-keyframes opacity-spec {
        0%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}