.similar-product-container {
    margin-top :1rem;
    background-color: white;
    padding: 1em;

}
.similar-product-title {
    font-size: 1.4rem;
    text-align: left;
    display: inline-block;
    font-weight: bold;
}
.similar-product-divider {
    border: 1px solid rgba(0,0,0, 0.1);
    margin-bottom: 2em;
}
.similar-product-element {
    width: 100%;
    padding: 1em;
}

@media screen and (max-width: 768px){
    .similar-product-element {
        padding: 0.5em;
    }
}