.table-of-content-container {
    padding: 0rem 1rem 1rem 1rem;
    border: 1px solid black;
    font-size: 14px;
    background-color: rgba(35, 166, 213, 0.1);
}

.toc-h2 {
    font-weight: bold;
    color: #23a6d5;
    display: table;

}

.toc-h3 {
    padding-left: 1rem;
    color: #23a6d5;
    padding-top: 0.5rem;
    display: table;
}

.toc-h2:hover, .toc-h3:hover {
    cursor: pointer;
    color: #23d5ab;
}

.toc-title {
    font-size: 1.5em;
    font-weight: bold;
    padding: 1em 0 0 0;
}
.toc-title span {
    margin-left: 0.5em;
}
.toc-right-btn-container {
    float: right;
    transition: 0.3s;
}
.toc-right-btn-container:hover {
    cursor: pointer;
}

.toc-detail {
    margin-top: 1em;
}

.icon-right {
    transition: 0.5s;
}