.product-body {
    margin-top: 1em;
    display: flex;
    flex-direction: row;
    background-color: white;
}
@media screen and (max-width: 1280px) {
    .product-hot-product {
        display: none;
    }
}
@media screen and (max-width: 768px){
    .product-body {
        flex-direction: column;
    }
}