.product-card-link {

}

.product-card-container {
    width: 100%;
    background-color: white;
    border-radius: 2px;
    /*-webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.1);*/
    /*-moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.1);*/
    /*box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.1);*/
    /*transition: transform 0.1s ease-in-out;*/
    /*border: 1px solid rgba(0, 0, 0, 0.1);*/
    overflow: hidden;
}

.product-card-container:hover {
    -webkit-box-shadow: 0px 0px 6px 3px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px 0px 6px 3px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 6px 3px rgba(0, 0, 0, 0.1);
    /*transform: translateY(-2px);*/
}

.product-card-image-container {
    width: 100%;
    position: relative;
    background-size: cover;
    background-position: center;
    padding-top: 100%;
    border-radius: 2px;
    overflow: hidden;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.1);
}
.product-card-container:hover .product-card-image-container {
    /*transform: scale(1.03);*/
}
.product-card-background {
    background-color: #424242;
    opacity: 0.4;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: none;
}

.product-card-container:hover .product-card-background {
    display: block;
}

.product-card-toCard {
    position: absolute;
    bottom: 0;
    box-sizing: border-box;
    transform: translate(0, 100%);
    width: 100%;
    font-size: 14px;
    background-color: red;
    opacity: 0;
    z-index: 2;
    padding: 0.75em;
    color: white;
    text-align: center;
    transition: background-color 0.2s ease-in, transform 0.2s ease-in, opacity 0.2s ease-in, left 0.2s ease-in, color 0.2s ease-in;
    /*width: 60%;*/
}

.product-card-toCard:hover {
    background-color: var(--third-bg-color);
    color: white;
}

.product-card-container:hover .product-card-toCard {
    transform: translate(0%, 0%);
    opacity: 1;
}

