.product-card-inner-text {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: -1;
    display: none;
}

.product-card-name {
    font-size: 0.9rem;
    line-height: 1.2rem;
    height: 2.4rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    color: black;
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
    text-align: center;
}

.product-card-detail {
    padding: 0.5em;
    position: relative;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.1);
    border-top:none;
}

.product-card-price-container {
    display: flex;
    flex-direction: row;
    height: 38px;
    margin-top: 0.4rem;
    justify-content: center;
}

.product-card-price {
    font-size: 12px;
    text-decoration: line-through;
    height: 16px;
    color: black;
    text-align: center;
}

.product-card-sale_price {
    font-weight: bold;
    color: var(--third-bg-color);
    font-size: 14px;
}

.product-card-sale-percent {
    font-size: 13px;
    font-weight: bold;
    position: absolute;
    color: white;
    padding: 5px;
    border-radius: 3px;
    align-self: flex-end;
    top: 0;
    left: 0;
    background-image: url("../../assets/sale.svg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 34px;
    height: 44px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-top: 5px;
    margin-left: 5px;
}

.product-card-sale-span {
    margin-bottom: 2px;
    margin-left: 5px;
}

.product-card-addCard:hover {
    background-color: #e0e0e0;
}

.product-card-amount {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 0.9em;
    opacity: 0.5;
    margin-bottom: 1.3em;
    margin-right: 1em
}

@media screen and (max-width: 500px) {
    .product-card-name {
        font-size: 0.8rem;
        line-height: 1rem;
        height: 2rem;
    }

    .product-card-container:hover {
        transform: none;
    }
}

.product-card-dialog-container {
    position: relative;
}

@media screen and (max-width: 768px) {
    .product-card-preview {
        display: none;
    }

    .product-card-dialog-container {
        display: none;
    }

    .product-card-toCard {
        display: none;
    }

    .product-card-price {
        font-size: 12px;
    }

    .product-card-sale_price {
        font-size: 14px;
    }

    .product-card-amount {
        font-size: 0.7em;
    }

    .product-card-container:hover .product-card-background {
        display: none;
    }
}