.style-1 {
    margin: auto;
    padding-top: 1rem;
    padding-bottom: 2rem;
    box-sizing: border-box;
    overflow: hidden;
    animation: opacity-des 0.5s ease-out;
}

@media screen and (min-width: 1151px) {
    .style-2 {
        width: 90%;
    }
}

@media screen and (max-width: 1280px) {
    .style-2 {
        width: 90%;
    }
}

@media screen and (max-width: 768px) {
    .style-2 {
        width: 100%;
    }
}
@keyframes opacity-des {
    0%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@-webkit-keyframes opacity-des {
        0%{
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}